@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;1,700&display=swap');

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body{
    background-color: #10002B;
    display: flex;
    align-items: center;
    justify-content:center ;
    min-height: 100vh;
}

.container-card{
    display: flex;
}

.card{
    background-color:#3C096C;
    width:387px;
    height:341px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.card:hover{
     box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
     transition: 200ms ease-in-out ;
     z-index: 1;
}
.profil{
    width: 135px;
    height: 135px;
    border-radius: 50%;
    background-color: #7B2CBF;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    margin-bottom: 20px;
}
.profil img{
    width: 90%;
    height: 90%;
    object-fit: cover;
    border-radius: 50%;
    
}
.card h1, p{
    font-family: 'Roboto', sans-serif;
    text-align: center;
}
.card h1{
    font-size: 25px;
    margin-bottom: 20px;
    color: #ffff;
}
.card p{
    font-weight: 500;
    width: 291px;
    font-size: 18px;
    margin: 0 auto;
    color: #D9D9D9;
}

@media screen and (max-width:376px){
    .container-card{
        display: flex;
        flex-direction: column;
        margin-top: 50px;
        margin-bottom: 50px;
    }
    
    .card{
        background-color:#3C096C;
        width:250px;
        height:250px;
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    
    .card:hover{
         box-shadow: rgba(0, 0, 0, 0.35) 0px 0px 15px;
         transition: 200ms ease-in-out ;
         z-index: 1;
    }
    .profil{
        width: 80px;
        height: 80px;
        border-radius: 50%;
        background-color: #7B2CBF;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 auto;
        margin-bottom: 20px;
    }
    .profil img{
        width: 90%;
        height: 90%;
        object-fit: cover;
        border-radius: 50%;
        
    }
    .card h1, p{
        font-family: 'Roboto', sans-serif;
        text-align: center;
    }
    .card h1{
        font-size: 15px;
        margin-bottom: 20px;
        color: #ffff;
    }
    .card p{
        font-weight: 500;
        width: 200px;
        font-size: 12px;
        margin: 0 auto;
        color: #D9D9D9;
    }
}